.App {
  text-align: center;
  margin-left: 5%;
  margin-right: 5% !important;
}



.app_form{
  padding: 20px;
  position: fixed;
  bottom: 0;
  z-index: 1;
  background-color: #e9e9eb;
  width: 85%;
  margin: 20px;
  border-radius: 30px;
}
.app_image{
  width: 100px;
}

.app_formControl{
  display: flex !important;
  flex-direction: row !important;
}

.app_input{
  flex: 1;
}

.app_iconButton{
  flex: 0;
}


